.one-contest{
    background-color: white;
    padding: 2rem;
    margin: 2rem 0 2rem 0;
    min-width: 33vw;
    max-width: 100rem;
}

.date{
    color: #92D3A8;
    /* font-family: 'Poppins'; */
    font-weight: 600;
}

.dt{
    display: flex;
    justify-content: space-between;
}

.da{
    font-weight: 600;
    font-size: 16.5px;
}

.name-c{
    word-wrap: break-word;      
}

@media screen and (max-width: 1000px) {
    .date,.da{
        font-size: 0.9em;
    }

    .one-contest{
        padding: 2.5em;
    }

    .name-c{
        font-size: medium;
    }
}