h2{
    margin: 0;
    border-radius: 30px 30px 0 0;
    font-weight: 700;
}

.sch{
    background-color: #E5EAF3;
    width: max-content;
    padding: 0 2rem 2rem 2rem;
    border-radius: 0 0 30px 30px;
    border-top: #E5EAF3;
    /* overflow: hidden; */
    max-height: 70vh;
    overflow: scroll;
}

.sch::-webkit-scrollbar {
    display: none;
}

.container{
    padding: 5rem;
}

.header{
    display: flex;
    background-color: white;
    border-radius: 30px 30px 0 0;
    justify-content: space-between;
    padding: 2rem;
    align-items: center;
}

button{
    display:block;
    height: 60px;
    width: 60px;
    border-radius: 50%; 
    border: none;
    background-color: #6C8DCA;
    color: #ffffff;
    cursor: pointer;
    font-weight: bold;
    font-size: large;
    /* padding: 100px; */
}

a{
    text-decoration: none;
    font-family: 'Nexa Bold';
}

@media screen and (max-width: 1000px) {
    .header{
        font-size: 3vw;
    }

    button{
        display: none;
    }

    .sch{
        padding: 1em;
        width: 70vw;
    }
}